@import url(https://fonts.googleapis.com/css2?family=Syne+Tactile&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App{
  text-align: center;
  height: auto;
  width: 100%;
  background-image: url('https://images.unsplash.com/photo-1533179856364-b2a4a66fa83e?ixlib=rb-1.2.1&ixid=eyJhcHBfaWQiOjEyMDd9&auto=format&fit=crop&w=1334&q=80');
}
.header{
height: auto;
}
.header-wrapper{
  background-color: rgba(0, 0, 0, 0.699);
}
.logo{
  position: absolute;
  top: 20px;
  left: 30px;
  border: 5px solid rgb(238, 28, 98);
  border-radius: 10px;
  padding: 10px;
  font-size: 2rem;
  color: rgb(238, 28, 98);
}
.head-title{
  font-family: 'Syne Tactile', cursive;
}
.head-title h1{
  margin: 0;
  padding-top: 20px;
  font-size: 4rem;
  color: rgb(238, 28, 98);
  text-transform: uppercase;
  font-weight: bolder;
}
.nav-items{
/* background-color: rgba(58, 39, 231, 0.253); */
}
.header ul{
list-style: none;
display: flex;
justify-content: center;
align-items: center;
margin: 0;
padding: 20px 0;
height: 400px;
}
.header ul a{
color: rgb(247, 243, 243);
font-size: 1.8rem;
width: 10%;
padding: 15px 0;
border: 1px solid rgb(250, 250, 250);
margin: 10px;
border-radius: 10px;
}
.btn-area{
display: flex;
justify-content: center;
align-items: center;
background-color: rgb(238, 28, 98);
padding: 15px 0;
}
.btn-area ul{
list-style: none;
display: flex;
justify-content: center;
align-items: center;
flex-direction: row;
flex-wrap: wrap;
min-height: 250px;
}
.btn-area a{
color: rgb(78, 74, 74);
font-size: 1.5rem;
padding: 25px 70px;
margin:  10px;
background: rgba(255, 255, 255, 0.904);
border-radius: 10px;
box-shadow: 2px 2px 7px rgb(180, 175, 175);
}
.btn-area a:hover{
  border: 2px solid rgba(230, 236, 230, 0.014);
  cursor: pointer;
}
.main{
  height: 65vh;
  position: relative;
  display: flex;
  background-image: url('https://i.pinimg.com/originals/20/44/51/204451e9377d0873ccc5c0f8e5372108.jpg');
  background-size: contain;
}
.main-left{
  width: 50%;
  height: 100%;
  background-color: rgba(241, 242, 245, 0.603);
}
.main-right{
  width: 50%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: rgba(241, 242, 245, 0.603);
}
.content-name{
height: 10vh;
font-size: 3rem;
margin-top: 15px;
padding-top: 15px;
color: rgb(238, 28, 98);
}
.content-box{
  display: flex;
  justify-content: center;
  align-items: center;
  height: 300px;
}
.content-photo{
height: 450px;
width: 530px;
margin: 5% 20%;
border-radius: 20px;
display: flex;
justify-content: center;
align-items: center;
border: 10px solid white;
}
.content-text{
width: 400px;
font-size: 1.8rem;
text-align: center;
padding: 30px;
color: rgb(97, 89, 89);

}
.footer{
  height: 10vh;
  background-color: rgb(17, 17, 87);
  display: flex;
  justify-content: space-evenly;
  align-items: center;
}
.footer p{
  letter-spacing: .3rem;
  color: white;
  font-size: 1.2rem;
}
.main-selected{
  background-color: rgb(238, 28, 98);
}
.sub-selected{
  background-color: tomato;
}
@media (max-width:1024px){
  .logo{
    display: none;
  }
}
@media (max-width:800px){
  .head-title h1{
    font-size: 3rem;
  }
  .header ul{
    flex-wrap: wrap;
    padding: 0;
  }
  .header ul a{
    color: rgb(243, 245, 243);
    width: 100px;
    
  }
  .btn-area a{
    width: 250px;
  }
}
@media (max-width:600px){
  .head-title h1{
    font-size: 2.3rem;
  }
  .header ul{
    height: 250px;
  }
  .header ul a{
    padding: 10px;
  }
  .btn-area a{
    width: 125px;
    color: teal;
    font-size: 1rem;
    padding: 5px 0;
  }
  .main{
    display: inline-block;
    height: auto;
    background-color: white;
    width: 100%;
  }
  .main-left{
    width: 100%;
    margin: 0;
  }
  .main-right{
    width: 100%;
  }
  .content-text{
    padding: 5px;
  }
  .content-box{
    width: 360;
  }
  .content-photo{
    width: 350px;
    height: 400px;
  }
  .footer{
    display: inline-block;
    width: 100%;
  }
}


